<template>
<div class="div">
  <div class="container" v-if="tools">
    <h3>{{blog.title}}</h3>
    <article>{{blog.content}}</article>
    <p>标签：</p>
    <ul>
      <li v-for="(item,index) in blog.taglist">{{item}}</li>
    </ul>
    <p>日期：{{blog.date}}</p>
    <div class="bottom">
      <div class="btn" @click="deleter">删除博客</div>
      <router-link class="btn" :to="'/edit/'+id">编辑博客</router-link>
    </div>
  </div>
  <div class="tools" v-else>
    <h5>删除成功</h5>
    <p>1s后返回到主页</p>
  </div>
</div>
</template>
<script>
export default {
  data:function(){
    return{
      id:this.$route.params.id,
      blog:{},
      tools:true
    }
  },
  methods:{
    //删除一条数据
    deleter:function(){
      let query = Bmob.Query('blogs');
      query.destroy(this.id).then(res =>{
        console.log('删除数据成功');
        this.tools=false;
        setTimeout(() => {
          this.$router.push('/');
        }, 1000);
        console.log(res);
      }).catch(err =>{
        console.log(err);
      })
    },
  },
  created(){
    // let query = Bmob.Query('blogs');
    // query.get(this.id).then(res =>{
    //   // console.log(res);
    //   this.blog=res;
    //   console.log(blog);
    // }).catch(err =>{
    //   console.log(err);
    // })
    //使用stroage获取本地存储的数据
    let data=JSON.parse(sessionStorage.getItem('data'));
    // console.log(typeof(data));
    data.forEach(element => {
      // console.log(element);
      if(element.objectId===this.id){
        this.blog=element;
      }
    });
    // console.log(data);
  }
}
</script>
<style scoped>

.container{
  width: 900px;
  margin: 1em auto;
  padding: 1em;
  border:1px solid #606266;
  background-color: #E4E7ED;
}
ul{
  padding-left: 0;
}
ul li{
  margin-right: 1em;
  display: inline;
  list-style: none;
}
.btn{
  cursor: pointer;
  display: inline-block;
  margin-right: 2em;
  padding: 0.2em 1em;
  background-color: #409EFF;
  text-decoration: none;
  color: #000000;
}
.btn:hover{
  background-color: #E6A23C;
  color: #909399;
}
.tools{

}
.tools h5{
  /* margin: 1em auto; */
  text-align: center;
  font-size: 20px;
  color: #909399;
}
.tools p{
  text-align: center;
}
</style>
