<template>
<div>
  <div class="container" v-if="isShow">
    <h2>写博客</h2>
    <h4>博客标题</h4>
    <input v-model="blog.title" type="text">
    <h4>博客内容</h4>
    <textarea v-model="blog.content" name="" id="" cols="30" rows="10" ></textarea>
    <div class="taginput">
      <label for="vue"><input id="vue" type="checkbox" value="vue" v-model="blog.taglist">vue</label>
      <label for="react"><input id="react" type="checkbox" value="react" v-model="blog.taglist">react</label>
      <label for="angular"><input id="angular" type="checkbox" value="angular" v-model="blog.taglist">angular</label>
      <label for="text1"><input type="checkbox" id="text1" value="前端" v-model="blog.taglist">前端</label>
      <label for="text2"><input type="checkbox" id="text2" value="日志" v-model="blog.taglist">日志</label>
      <label for="text3"><input type="checkbox" id="text3" value="android" v-model="blog.taglist">android</label>
    </div>
    <input type="date" v-model="blog.date">
    <div class="button" @click="addblog">
      发布博客
    </div>
  </div>
  <div v-else class="bottom">
    <h3>博客发布成功</h3>
  </div>
</div>
</template>
<script>
export default {
  data:function(){
    return{
      blog:{
        title:'',
        content:'',
        date:'',
        taglist:[]
      },
      isShow:true
    }
  },
  methods:{
    //添加一条博客数据
    addblog:function(){
      this.isShow=false;
      let query = Bmob.Query('blogs');
      query.set('title',this.blog.title);
      query.set('content',this.blog.content);
      query.set('date',this.blog.date);
      query.set('taglist',this.blog.taglist);
      query.save().then(res =>{
        console.log(res);
        console.log('提交数据到bmob数据库');
      }).catch(err =>{
        console.log(err);
      })
      
    }
  }
}
</script>
<style scoped>
.container{
  width: 900px;
  margin: 0 auto;
}
.button{
  cursor: pointer;
  margin-top: 1.5em;
  text-align: center;
  width: 100px;
  padding: 10px 20px;
  background-color: #909399;
}
.button:hover{
  background-color: #409EFF;
  color: #303133;
}
input[type='text']{
  display: block;
  width:100%;
  height: 2em;
}
textarea{
  display: block;
  width: 100%;
  height: 400px;
  font-size: 16px;
}
.taginput{
  margin-top: 1em;
  margin-bottom: 1em;
}
.taginput label{
  height: 2em;
  line-height: 2em;
  margin-right: 1em;
}
.bottom{
  margin-top: 2em;
  text-align: center;
}
</style>
