<template>
  <form class="container">
    <h3>{{title}}</h3>
    <div class="content">
      <p>用户名</p>
      <div class="inp">
        <input type="text" placeholder="请输入用户名" v-model="username">
      </div>
      <p class="tip" v-if="istip">用户名已注册</p>
      <p>密码</p>
      <div class="inp">
        <input type="password" placeholder="请输入密码" v-model="password">
      </div>
      <p v-if="isl" class="isl">用户名和密码不一致</p>
      <p v-if="isshow">确认密码</p>
      <div class="inp" v-if="isshow">
        <input type="password" placeholder="再次输入密码" v-model="pw">
      </div>
      <div class="btn-con">
        <div class="btn" @click="login">登陆</div>
        <div class="btn" @click="signin">注册</div>
      </div>
    </div>
  </form>
</template>

<script>
export default {
  data() {
    return {
      isshow: false,
      title: "用户登陆",
      username: "",
      password: "",
      pw: "",
      islogin: true,
      tip:"",
      istip:false,
      isl:false
    };
  },
  methods: {
    //注册
    signin() {
      if (this.islogin) {
        this.isshow = true;
        this.title = "用户注册";
        this.islogin = false;
      } else {
        if (this.password === this.pw) {
          // let query = Bmob.Query("_User");
          //先获取用户表中的数据，进行对比，如果存在则，提示用户已经注册过，如果没有则，添加数据到用户表中
          // let arr;
          // let dataJson;
          // query.find().then(res=>{
          //   console.log(res);
          // })

          // query.set("username", this.username);
          // query.set("password", this.password);
          // query.save().then(res => {
          //     console.log(res);
          //     console.log("提交数据到bmob数据库");
          //   }).catch(err => {
          //     console.log(err);
          //   });

          let params={
            username:this.username,
            password:this.password
          }
          Bmob.User.register(params).then(res=>{
            console.log(res);
            alert('注册成功');
            this.$router.push('/');
          }).catch(err=>{
            console.log(err);
            this.istip=true;
          })

          // alert("注册成功");
        } else {
          alert("两次输入的密码不一致，请重新输入");
        }
      }
    },
    //登陆
    login() {
      if (this.islogin) {
        //验证登陆信息
        Bmob.User.login(this.username,this.password).then(res=>{
          console.log(res);
          let iflag=false;
          let istou=true
          this.$store.dispatch('setUser',iflag);
          this.$store.dispatch('setistou',istou);
          sessionStorage.setItem('flag','islogin');
          this.$router.push('/');
        }).catch(err=> {
          console.log(err);
          this.isl=true;
        })
        // alert("登陆成功");
      } else {
        this.isshow = false;
        this.title = "用户登陆";
        this.islogin = true;
        this.istip=false;
      }
    }
  }
};
</script>


<style scoped>
.isl{
  color: red;
}
.tip{
  color: red;
}
.container {
  /* margin-top: 200px; */
  width: 400px;
  margin: 30px auto;
  border: 1px solid black;
}
.container h3 {
  width: 100%;
  height: 40px;
  text-align: center;
  background: #eee;
  line-height: 40px;
}
.container input {
  width: 100%;
  height: 40px;
  box-sizing: border-box;
  padding: 5px 10px;

  border: none;
  outline: none;
}
.container input,
.container p {
  /* margin-top: 10px; */
}
.content {
  width: 250px;
  margin: 0 auto;
}
.btn {
  width: 100px;
  height: 25px;
  background-color: #0099cc;
  padding: 5px 10px;
  margin-top: 10px;
  border-radius: 5px;
  text-align: center;
  line-height: 25px;
  cursor: pointer;
}
.btn-con {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-around;
  margin-bottom: 20px;
}
.btn:hover {
  color: orange;
  border-radius: 5px;
}
p {
  margin-top: 10px;
}
.inp {
  border: 1px solid #eee;
  margin-top: 10px;
  border-radius: 5px;
}
</style>
