<template>
  <div class="container">
    <div class="search">
      <input type="text" placeholder="搜索..." v-model="search">
    </div>
    <div class="content">
      <div class="card" v-for="(blog,index) in filterdate">
        <router-link :to="'/blog/'+blog.objectId" class="title" v-atext>{{blog.title}}</router-link>
        <article>{{blog.content}}</article>
        <p class="time">{{blog.date}}</p>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data:function(){
    return{
      blogs:[],
      search:''
    }
  },
  created (){

    //使用web storage 进行缓存数据，减少请求的次数。使用web存储。
    //1、在组件被创建后，请求数据，并存储数据，
    //2、下次请求的时候在更新存储的数据。


    let dataJson;
    let arr;
    //获取所有数据
    let query = Bmob.Query('blogs');
    query.find().then(res =>{
        // console.log(res);
        // this.blogs=res.reverse();
        //存储数据
        arr=res.reverse();
        dataJson=JSON.stringify(arr);
        sessionStorage.setItem('data',dataJson);
        let data=sessionStorage.getItem('data');
        let objectData=JSON.parse(data);
        this.blogs=objectData;
        // console.log(this.blogs);
      }).catch(err =>{
        console.log(err);
      })
      
      // console.log('结束');
       //

      // if(typeof(Storage)!=="underfined"){

        
      // }else{
        // console.log('无法使用sessionStorage');
      // }
  },
  computed:{
    //通过search 关键字 过滤需要的数据
    filterdate:function(){
      let search=this.search.toUpperCase();
      let date=this.blogs.filter(blog =>{
        // cv.title.indexOf(this.search);
        return blog.title.toUpperCase().includes(search);
      })
      return date;
    }
  }
}
</script>
<style scoped>
.container{
  margin: 2em auto;
  width: 900px;
  /* margin: 2em; */
}
.search input{
  display: block;
  width: 100%;
  height: 2em;
  font-size: 1em;
  border: 1px solid #DCDFE6;
  text-indent: 1em;
  border-radius:5px;
}
.card{
  border: 1px solid #DCDFE6;
  border-radius: 5px;
  margin-top: 1.5em;
}
.card .title{
  display: block;
  margin-left: 0.5em;
  margin-top: 0.5em;  
}
.card article{
  margin-left: 0.5em;
  margin-top: 1em;
  margin-bottom: 1em;
}
.card .time{
  margin-left: 0.5em;

  color: #909399;
}
a{
  color: #303133;
  font-weight: 700;
}
</style>


